<template>
    <div class="product-list-page">
      <div class="container">
        <div class="page-header">
          <h1>新鲜农产品</h1>
          <div class="header-right">
            <el-input
              v-model="searchQuery"
              placeholder="搜索产品"
              prefix-icon="el-icon-search"
              clearable
              @clear="handleSearchClear"
              @keyup.enter="handleSearch"
              class="search-input">
            </el-input>
          </div>
        </div>
        
        <div class="filter-section">
          <el-card>
            <div class="filter-row">
              <div class="filter-label">分类：</div>
              <div class="filter-options">
                <el-radio-group v-model="filters.category" @change="applyFilters">
                  <el-radio-button label="">全部</el-radio-button>
                  <el-radio-button label="table">蔬菜</el-radio-button>
                  <el-radio-button label="chair">水果</el-radio-button>
                </el-radio-group>
              </div>
            </div>
            
            <div class="filter-row">
              <div class="filter-label">营养特性：</div>
              <div class="filter-options">
                <el-checkbox-group v-model="filters.ecoFeatures" @change="applyFilters">
                  <el-checkbox label="recyclable">低脂肪</el-checkbox>
                  <el-checkbox label="organic">低糖</el-checkbox>
                  <el-checkbox label="energy_efficient">高蛋白</el-checkbox>
                  <el-checkbox label="low_voc">低热量</el-checkbox>
                  <el-checkbox label="sustainable_wood">高膳食纤维</el-checkbox>
                </el-checkbox-group>
              </div>
            </div>
            
            <div class="filter-actions">
              <el-button type="primary" @click="applyFilters">应用筛选</el-button>
              <el-button @click="resetFilters">重置</el-button>
            </div>
          </el-card>
        </div>
        
        <div class="sort-section">
          <el-radio-group v-model="sortBy" size="small" @change="handleSortChange">
            <el-radio-button label="popularity">热门</el-radio-button>
            <el-radio-button label="newest">最新</el-radio-button>
            <el-radio-button label="price_asc">价格从低到高</el-radio-button>
            <el-radio-button label="price_desc">价格从高到低</el-radio-button>
          </el-radio-group>
          
          <div class="view-mode">
            <el-button-group>
              <el-button 
                :type="viewMode === 'grid' ? 'primary' : ''" 
                icon="el-icon-s-grid"
                @click="viewMode = 'grid'">
              </el-button>
              <el-button 
                :type="viewMode === 'list' ? 'primary' : ''" 
                icon="el-icon-menu"
                @click="viewMode = 'list'">
              </el-button>
            </el-button-group>
          </div>
        </div>
        
        <div v-if="loading" class="products-loading">
          <div class="skeleton-container" :class="{ 'list-view': viewMode === 'list' }">
            <el-skeleton :count="8" animated>
              <template #template>
                <div class="skeleton-item">
                  <el-skeleton-item variant="image" style="width: 100%; height: 200px;" />
                  <div style="padding: 14px;">
                    <el-skeleton-item variant="h3" style="width: 70%;" />
                    <div style="margin-top: 16px; display: flex; justify-content: space-between;">
                      <el-skeleton-item variant="text" style="width: 30%;" />
                      <el-skeleton-item variant="text" style="width: 30%;" />
                    </div>
                  </div>
                </div>
              </template>
            </el-skeleton>
          </div>
        </div>
        
        <div v-else-if="error" class="products-error">
          <el-empty description="加载产品失败">
            <el-button type="primary" @click="fetchProducts">重试</el-button>
          </el-empty>
        </div>
        
        <div v-else-if="filteredProducts.length === 0" class="no-products">
          <el-empty description="没有找到匹配的产品">
            <el-button @click="resetFilters">重置筛选条件</el-button>
          </el-empty>
        </div>
        
        <div v-else :class="['products-container', { 'list-view': viewMode === 'list' }]">
          <div 
            v-for="product in filteredProducts" 
            :key="product.id" 
            class="product-card"
            @click="viewProductDetail(product)">
            
            <div class="product-image">
              <el-image 
                :src="getFullImageUrl(product.image_url || product.image || product.product_image || '')" 
                :alt="product.name"
                @error="() => console.warn('产品图片加载失败:', product.product_id || product.id)">
                <template #error>
                  <div class="image-error-slot">
                    <i class="el-icon-picture-outline"></i>
                    <p>图片加载失败</p>
                  </div>
                </template>
              </el-image>
              <div v-if="product.discount" class="discount-badge">
                -{{ product.discount }}%
              </div>
            </div>
            
            <div class="product-body">
              <h3 class="product-name">{{ product.name }}</h3>
              
              <div class="eco-badges">
                <el-tag 
                  v-if="product.eco_certification" 
                  size="small" 
                  type="success">
                  {{ product.eco_certification }}
                </el-tag>
                <el-tag 
                  v-if="product.is_recyclable" 
                  size="small" 
                  type="info">
                  可回收
                </el-tag>
              </div>
              
              <div class="product-price">
                <span class="current-price">¥{{ product.price.toFixed(2) }}</span>
                <span v-if="product.original_price" class="original-price">
                  ¥{{ product.original_price.toFixed(2) }}
                </span>
              </div>
              
              <div class="product-actions">
                <el-button 
                  type="primary" 
                  size="small" 
                  icon="el-icon-shopping-cart-2"
                  @click.stop="addToCart(product)">
                  加入购物车
                </el-button>
                <el-button
                  size="small"
                  icon="el-icon-view"
                  @click.stop="viewProductDetail(product)">
                  查看详情
                </el-button>
              </div>
            </div>
          </div>
        </div>
        
        <div v-if="totalPages > 1" class="pagination-container">
          <el-pagination
            background
            :current-page="currentPage"
            :page-size="pageSize"
            :total="totalProducts"
            layout="prev, pager, next"
            @current-change="handlePageChange">
          </el-pagination>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { ref, reactive, computed, watch, onMounted } from 'vue'
  import { useRouter, useRoute } from 'vue-router'
  import { useStore } from 'vuex'
  import { ElMessage } from 'element-plus'
  import { getFullImageUrl } from '../utils/index'
  import axios from 'axios'
  
  export default {
    name: 'ProductList',
    setup() {
      const router = useRouter()
      const route = useRoute()
      const store = useStore()
      
      // 状态变量
      const products = ref([])
      const loading = ref(false)
      const error = ref(false)
      const currentPage = ref(1)
      const pageSize = ref(20)
      const totalProducts = ref(0)
      const searchQuery = ref('')
      const viewMode = ref('grid') // grid 或 list
      const sortBy = ref('popularity') // popularity, newest, price_asc, price_desc
      
      // 筛选条件
      const filters = reactive({
        category: '',
        ecoFeatures: []
      })
      
      // 计算总页数
      const totalPages = computed(() => {
        return Math.ceil(totalProducts.value / pageSize.value)
      })
      
      // 应用筛选和排序后的产品列表
      const filteredProducts = computed(() => {
        console.log('开始筛选产品，原始数量:', products.value.length)
        if (products.value.length > 0) {
          console.log('产品数据示例:', products.value[0])
        }
        
        let result = [...products.value]
        
        // 应用搜索
        if (searchQuery.value) {
          const query = searchQuery.value.toLowerCase()
          result = result.filter(product => 
            (product.name && product.name.toLowerCase().includes(query)) || 
            (product.description && product.description.toLowerCase().includes(query))
          )
          console.log('搜索筛选后数量:', result.length)
        }
        
        // 应用分类筛选
        if (filters.category) {
          console.log('应用分类筛选:', filters.category)
          console.log('产品中的分类字段:', products.value.map(p => `${p.product_id}: ${p.category || p.category_id}`))
          
          result = result.filter(product => {
            const productCategory = product.category || product.category_id
            const matches = productCategory === filters.category
            console.log(`产品 ${product.product_id} 分类 ${productCategory} 是否匹配 ${filters.category}: ${matches}`)
            return matches
          })
          console.log('分类筛选后数量:', result.length)
        }
        
        // 应用生态特性筛选
        if (filters.ecoFeatures.length > 0) {
          console.log('应用生态特性筛选:', filters.ecoFeatures)
          
          result = result.filter(product => {
            const matches = filters.ecoFeatures.some(feature => {
              if (feature === 'recyclable') return product.is_recyclable
              if (feature === 'organic') return product.eco_certification === '有机认证'
              if (feature === 'energy_efficient') return product.eco_certification === '能源之星认证'
              if (feature === 'low_voc') return product.eco_certification === '低VOC认证'
              if (feature === 'sustainable_wood') return product.eco_certification === '森林管理委员会认证'
              return false
            })
            return matches
          })
          console.log('环保特性筛选后数量:', result.length)
        }
        
        // 应用排序
        if (sortBy.value === 'newest') {
          result.sort((a, b) => new Date(b.created_at || 0) - new Date(a.created_at || 0))
        } else if (sortBy.value === 'price_asc') {
          result.sort((a, b) => (a.price || 0) - (b.price || 0))
        } else if (sortBy.value === 'price_desc') {
          result.sort((a, b) => (b.price || 0) - (a.price || 0))
        } else if (sortBy.value === 'popularity') {
          result.sort((a, b) => (b.sold_count || 0) - (a.sold_count || 0))
        }
        
        console.log('最终显示产品数量:', result.length)
        return result
      })
      
      // 从URL参数初始化筛选条件
      const initFromUrlParams = () => {
        console.log('初始化筛选条件，URL参数:', route.query)
        
        // 重置所有筛选条件
        filters.category = ''
        filters.ecoFeatures = []
        searchQuery.value = ''
        currentPage.value = 1
        
        // 从URL应用筛选条件
        if (route.query.category) {
          filters.category = route.query.category
        }
        
        if (route.query.search) {
          searchQuery.value = route.query.search
        }
        
        if (route.query.page) {
          currentPage.value = parseInt(route.query.page) || 1
        }
        
        console.log('初始化后的筛选条件:', { 
          category: filters.category,
          ecoFeatures: filters.ecoFeatures,
          searchQuery: searchQuery.value,
          page: currentPage.value
        })
      }
      
      // 更新URL参数
      const updateUrlParams = () => {
        const query = {}
        
        if (filters.category) {
          query.category = filters.category
        }
        
        if (searchQuery.value) {
          query.search = searchQuery.value
        }
        
        if (currentPage.value > 1) {
          query.page = currentPage.value
        }
        
        router.replace({ query })
      }
      
      // 获取产品列表
      const fetchProducts = async () => {
        loading.value = true
        error.value = false
        
        try {
          // 构建API请求参数
          const params = {
            page: currentPage.value,
            size: pageSize.value || 20
          }
          
          console.log('获取产品列表，参数:', params)
          
          // 添加分类过滤
          if (filters.category) {
            params.category_id = filters.category
            console.log('添加分类过滤:', filters.category)
          }
          
          // 添加搜索关键词
          if (searchQuery.value) {
            params.keyword = searchQuery.value
            console.log('添加搜索关键词:', searchQuery.value)
          }
          
          // 添加排序条件
          if (sortBy.value) {
            params.sort = sortBy.value
            console.log('添加排序条件:', sortBy.value)
          }
          
          console.log('完整请求参数:', params)
          
          // 发送API请求
          const response = await axios.get('/api/products/list', { params })
          
          console.log('API响应状态:', response.status)
          console.log('API响应数据:', response.data)
          
          if (response.data && response.data.code === 200 && response.data.data && response.data.data.list) {
            const productList = response.data.data.list
            const total = response.data.data.total || 0
            
            console.log(`产品列表获取成功: ${productList.length}个产品，总数:${total}`)
            
            if (productList.length > 0) {
              console.log('产品示例:', JSON.stringify(productList[0]))
            }
            
            products.value = productList
            totalProducts.value = total
          } else {
            console.warn('API返回格式不符合预期:', response.data)
            products.value = []
            totalProducts.value = 0
          }
        } catch (err) {
          console.error('获取产品列表失败:', err)
          if (err.response) {
            console.error('错误状态码:', err.response.status)
            console.error('错误数据:', err.response.data)
          }
          error.value = true
          products.value = []
        } finally {
          loading.value = false
        }
      }
      
      // 处理页码变化
      const handlePageChange = (page) => {
        currentPage.value = page
        fetchProducts()
        updateUrlParams()
        // 滚动到页面顶部
        window.scrollTo(0, 0)
      }
      
      // 应用筛选
      const applyFilters = () => {
        console.log('应用筛选条件:', { 
          category: filters.category,
          ecoFeatures: filters.ecoFeatures
        })
        currentPage.value = 1
        updateUrlParams()
        // 重新获取产品
        fetchProducts()
      }
      
      // 重置筛选条件
      const resetFilters = () => {
        console.log('重置所有筛选条件')
        filters.category = ''
        filters.ecoFeatures = []
        currentPage.value = 1
        searchQuery.value = ''
        updateUrlParams()
        // 重新获取产品数据
        fetchProducts()
      }
      
      // 处理搜索
      const handleSearch = () => {
        console.log('执行搜索:', searchQuery.value)
        currentPage.value = 1
        updateUrlParams()
        // 重新获取产品数据
        fetchProducts()
      }
      
      // 处理清除搜索
      const handleSearchClear = () => {
        console.log('清除搜索')
        searchQuery.value = ''
        handleSearch()
      }
      
      // 处理排序变化
      const handleSortChange = () => {
        // 不更新URL参数，仅前端排序
      }
      
      // 查看产品详情
      const viewProductDetail = (product) => {
        // 使用product_id而不是id，与后端API匹配
        const productId = product.product_id || product.id
        router.push(`/product/${productId}`)
      }
      
      // 加入购物车
      const addToCart = (product) => {
        store.dispatch('cart/addToCart', {
          product,
          quantity: 1
        })
        ElMessage.success('已添加到购物车')
      }
      
      // 监听路由参数变化
      watch(() => route.query, (newQuery) => {
        initFromUrlParams()
      })
      
      onMounted(() => {
        initFromUrlParams()
        fetchProducts()
      })
      
      return {
        products,
        filteredProducts,
        loading,
        error,
        currentPage,
        pageSize,
        totalProducts,
        totalPages,
        searchQuery,
        viewMode,
        sortBy,
        filters,
        fetchProducts,
        handlePageChange,
        applyFilters,
        resetFilters,
        handleSearch,
        handleSearchClear,
        handleSortChange,
        viewProductDetail,
        addToCart,
        getFullImageUrl
      }
    }
  }
  </script>
  
  <style scoped>
